import React from 'react'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'


export default function card(props) {
    return (
        <div>
            <Card className="datas">
                <Card.Img variant="top" src= {props.data.Imagen} />
                <Card.Body>
                    <Card.Title className="text-dark"> {props.data.nombre} </Card.Title>
                    <Card.Text className="text-dark" > {props.data.descripcion} </Card.Text>
                    <Button variant="outline-primary">Ver mas</Button>
                </Card.Body>
            </Card>
        </div>
    )
}
